import * as echarts from '../../components/echarts/echarts.simple.min.js';
import mpvueEcharts from '../../components/mpvue-echarts/src/echarts.vue';
const cityList = [{
	value: 55,
	name: '北京'
}, {
	value: 38,
	name: '上海'
}, {
	value: 20,
	name: '广州'
}];
let pieOption = {
	animation: false,
	backgroundColor: '#F8F8F8',
	color: ['#37A2DA', '#32C5E9', '#67E0E3', '#91F2DE', '#FFDB5C', '#FF9F7F'],
	series: [{
		label: {
			normal: {
				fontSize: 14
			}
		},
		type: 'pie',
		center: ['50%', '50%'],
		radius: [0, '60%'],
		data: [],
		itemStyle: {
			emphasis: {
				shadowBlur: 10,
				shadowOffsetX: 0,
				shadowColor: 'rgba(0, 2, 2, 0.3)'
			}
		}
	}]
};
let lineOption = {
	animation: true,
	color: ['#fff'],
	grid: {
		x: 0,
		x2: 0,
		y: 0,
		y2: 0,
		// show:false
	},
	calculable: false,
	xAxis: [{
		type: 'category',
		data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
		show: false
	}],
	yAxis: [{
		type: 'value',
		show: false,
		splitArea: {
			show: true
		}
	}],
	series: [{
		name: '蒸发量',
		type: 'line',
		smooth: true,
		symbol: "none",
		data: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
	}]
};
export default {
	data() {
		return {
			// echarts: echarts,
			updateStatus: false
		}
	},
	onLoad() {
		pieOption.series[0].data = cityList.slice(0);
	},
	methods: {
		goBrowser() {




			uni.showModal({
				content: '请复制链接在浏览器里打开',
				showCancel: false
			})

		},
		lineInit(canvas, width, height) {
			echarts.setCanvasCreator(() => canvas);
			let lineChart = echarts.init(canvas, null, {
				width: width,
				height: height
			})
			canvas.setChart(lineChart)
			lineChart.setOption(lineOption)
			return lineChart
		}
	},
	components: {
		mpvueEcharts
	}
}
